body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.CounterPage .count-row {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-bottom: 12px;
}

.CounterPage .count {
  -webkit-flex: 1 1 auto;
          flex: 1 1 auto;
}

.CounterPage .count .count-name {
  font-weight: 600;
}

.CounterPage .count .count-value {
  min-width: 80px;
  display: inline-block;
}

.Base64Page textarea {
  resize: none;
}

.Base64Page textarea.error {
  border: 1px solid var(--color5);
}

.Base64Page-pane-wrapper {
  display: -webkit-flex;
  display: flex;
}

.Base64Page-pane {
  -webkit-flex: 1 1;
          flex: 1 1;
}

:root {
  --color1: #004c77;
  --color2: #82b7c5;
  --color3: rgba(245, 239, 166, 1);
  --color4: rgba(245, 166, 92, 1);
  --color5: rgba(171, 52, 40, 1);
}

* {
  box-sizing: border-box;
}

// https://coolors.co/2d728f-3b8ea5-f5efa6-f5a65c-ab3428

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #004c77;
  background-color: var(--color1);
  padding: 0.8rem 2rem;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: flex-start;
          align-items: flex-start;
  -webkit-justify-content: flex-start;
          justify-content: flex-start;
  font-size: 1.5rem;
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
  font-size: 18px;
  line-height: 1.3;
  color: #2d2d2d;
  margin: 0;
  padding: 0;
}

.content-container {
  margin: 0 auto;
  padding: 0;
  max-width: 1200px;
}

#page-content.content-container {
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 0 15px;
}

h1 {
  font-weight: 600;
  font-size: 2rem;
}

a, a:focus, a:hover, a:visited {
  color: #3F729B;
}

textarea, button, input, select {
  font-size: 1rem;
}

textarea {
  width: 100%;
  padding: 10px;
  min-height: 200px;
  resize: vertical;
}

nav {
  margin-left: 1rem;
  display: inline-block;
}

nav li {
  list-style: none;
  display: inline-block;
  font-size: 1rem;
  margin: 0 0.3rem;
}

nav li a,
nav li a:hover,
nav li a:visited {
  color: white;
}

